import React, { useState, useEffect } from 'react'
import { useQuery } from 'react-query'
import {
  Play,
  Pause,
  RefreshCw,
  Filter,
  Download,
  BarChart3,
  Clock,
  CheckCircle,
  XCircle,
  AlertCircle,
  Folder,
  Zap
} from 'lucide-react'
import apiClient from '../services/api'
import SimulationRunner from '../components/solver/SimulationRunner'

// Composant de visualisation simplifi (remplace ResultsVisualization temporairement)
const SimpleResultsVisualization = ({ simulation }) => {
  if (!simulation || simulation.status !== 'completed') {
    return (
      <div className="bg-white rounded-lg shadow p-6">
        <div className="text-center py-8">
          <BarChart3 className="h-12 w-12 text-gray-400 mx-auto mb-4" />
          <p className="text-gray-600">
            {!simulation 
              ? "Slectionnez une simulation" 
              : "Visualisation disponible aprs compltion"}
          </p>
        </div>
      </div>
    )
  }

  return (
    <div className="bg-white rounded-lg shadow p-6">
      <h3 className="text-lg font-semibold text-gray-900 mb-4">
        Rsultats de simulation
      </h3>
      <div className="space-y-4">
        <div className="grid grid-cols-2 gap-4">
          <div className="bg-blue-50 p-4 rounded-lg">
            <p className="text-sm text-gray-600">Statut</p>
            <p className="text-lg font-semibold text-green-600">Termin</p>
          </div>
          <div className="bg-green-50 p-4 rounded-lg">
            <p className="text-sm text-gray-600">Temps d'excution</p>
            <p className="text-lg font-semibold text-gray-900">
              {simulation.execution_time || "N/A"}s
            </p>
          </div>
        </div>
        <div className="border-t pt-4">
          <h4 className="font-medium text-gray-900 mb-2">Donnes disponibles</h4>
          <ul className="space-y-2">
            <li className="flex items-center text-sm">
              <CheckCircle className="h-4 w-4 text-green-500 mr-2" />
              Champ de vitesse
            </li>
            <li className="flex items-center text-sm">
              <CheckCircle className="h-4 w-4 text-green-500 mr-2" />
              Champ de pression
            </li>
            <li className="flex items-center text-sm">
              <CheckCircle className="h-4 w-4 text-green-500 mr-2" />
              Mtriques de convergence
            </li>
          </ul>
        </div>
      </div>
    </div>
  )
}

const Simulations = () => {
  const [selectedSimulation, setSelectedSimulation] = useState(null)
  const [filter, setFilter] = useState('all')
  const [searchTerm, setSearchTerm] = useState('')

  // Donnes de simulation mockes (en attendant l'API)
  const [simulations, setSimulations] = useState([
    {
      id: 'sim-001',
      name: 'Cavit entrane - Re=1000',
      status: 'completed',
      physics_model_id: 'navier-stokes-1',
      created_at: '2024-01-15T10:30:00Z',
      input_parameters: { reynolds: 1000, nx: 50, ny: 50 },
      execution_time: 45.2
    },
    {
      id: 'sim-002',
      name: 'Transfert thermique - Plaque chaude',
      status: 'running',
      physics_model_id: 'heat-transfer-1',
      created_at: '2024-01-15T11:15:00Z',
      input_parameters: { temperature: 300, nx: 40, ny: 40 },
      execution_time: 120.5
    },
    {
      id: 'sim-003',
      name: 'coulement laminaire - Tube',
      status: 'pending',
      physics_model_id: 'navier-stokes-2',
      created_at: '2024-01-14T14:20:00Z',
      input_parameters: { reynolds: 500, nx: 60, ny: 30 },
      execution_time: null
    }
  ])

  const filteredSimulations = simulations.filter(sim => {
    if (filter !== 'all' && sim.status !== filter) return false
    if (searchTerm && !sim.name.toLowerCase().includes(searchTerm.toLowerCase())) return false
    return true
  })

  const getStatusIcon = (status) => {
    switch (status) {
      case 'running': return <RefreshCw className="h-4 w-4 animate-spin" />
      case 'completed': return <CheckCircle className="h-4 w-4" />
      case 'failed': return <XCircle className="h-4 w-4" />
      case 'pending': return <Clock className="h-4 w-4" />
      default: return <AlertCircle className="h-4 w-4" />
    }
  }

  const getStatusColor = (status) => {
    switch (status) {
      case 'running': return 'text-blue-600 bg-blue-100'
      case 'completed': return 'text-green-600 bg-green-100'
      case 'failed': return 'text-red-600 bg-red-100'
      case 'pending': return 'text-yellow-600 bg-yellow-100'
      default: return 'text-gray-600 bg-gray-100'
    }
  }

  const handleRunSimulation = async (simulationData) => {
    try {
      // Simulation de cration
      const newSim = {
        id: `sim-${Date.now()}`,
        name: simulationData.name,
        status: 'pending',
        physics_model_id: simulationData.physics_model_id || 'default',
        created_at: new Date().toISOString(),
        input_parameters: simulationData.input_parameters || {},
        execution_time: null
      }
      
      setSimulations(prev => [newSim, ...prev])
      
      // Simuler le lancement
      setTimeout(() => {
        setSimulations(prev => 
          prev.map(sim => 
            sim.id === newSim.id 
              ? { ...sim, status: 'running' } 
              : sim
          )
        )
        
        // Simuler la compltion aprs 5 secondes
        setTimeout(() => {
          setSimulations(prev => 
            prev.map(sim => 
              sim.id === newSim.id 
                ? { 
                    ...sim, 
                    status: 'completed', 
                    execution_time: 42.5,
                    pinn_predictions: { velocity_field: [], pressure_field: [] }
                  } 
                : sim
            )
          )
        }, 5000)
      }, 1000)
      
    } catch (error) {
      console.error('Failed to run simulation:', error)
    }
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Simulations PINN</h1>
          <p className="text-gray-600 mt-2">
            Grez et excutez vos simulations acclres par IA
          </p>
        </div>
        <div className="flex space-x-3">
          <button
            onClick={() => window.location.reload()}
            className="flex items-center px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50"
          >
            <RefreshCw className="h-5 w-5 mr-2" />
            Actualiser
          </button>
        </div>
      </div>

      {/* Main Content */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Left Panel - Simulation List */}
        <div className="lg:col-span-2 space-y-6">
          {/* Controls */}
          <div className="bg-white rounded-lg shadow p-4">
            <div className="flex flex-col md:flex-row md:items-center space-y-4 md:space-y-0 md:space-x-4">
              <div className="flex-1">
                <input
                  type="text"
                  placeholder="Rechercher une simulation..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>
              <div className="flex space-x-2">
                <select
                  value={filter}
                  onChange={(e) => setFilter(e.target.value)}
                  className="px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="all">Tous les statuts</option>
                  <option value="pending">En attente</option>
                  <option value="running">En cours</option>
                  <option value="completed">Termin</option>
                  <option value="failed">chou</option>
                </select>
                <button className="flex items-center px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50">
                  <Filter className="h-5 w-5 mr-2" />
                  Filtres
                </button>
              </div>
            </div>
          </div>

          {/* Simulation Runner */}
          <div className="bg-white rounded-lg shadow">
            <SimulationRunner onRunSimulation={handleRunSimulation} />
          </div>

          {/* Simulations List */}
          <div className="bg-white rounded-lg shadow">
            <div className="p-6">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">
                Simulations Rcentes
              </h3>
              
              {filteredSimulations.length === 0 ? (
                <div className="text-center py-8">
                  <Folder className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                  <p className="text-gray-600 mt-2">Aucune simulation trouve</p>
                </div>
              ) : (
                <div className="space-y-3">
                  {filteredSimulations.map((simulation) => (
                    <div
                      key={simulation.id}
                      className={`p-4 border rounded-lg cursor-pointer transition-colors ${
                        selectedSimulation?.id === simulation.id
                          ? 'border-blue-500 bg-blue-50'
                          : 'border-gray-200 hover:bg-gray-50'
                      }`}
                      onClick={() => setSelectedSimulation(simulation)}
                    >
                      <div className="flex justify-between items-start">
                        <div>
                          <h4 className="font-medium text-gray-900">
                            {simulation.name}
                          </h4>
                          <p className="text-sm text-gray-600 mt-1">
                            Modle: {simulation.physics_model_id}
                          </p>
                          <div className="flex items-center mt-2">
                            <Clock className="h-4 w-4 text-gray-400 mr-1" />
                            <span className="text-sm text-gray-500">
                              {new Date(simulation.created_at).toLocaleDateString()}
                            </span>
                          </div>
                        </div>
                        <div className="flex items-center space-x-3">
                          <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getStatusColor(simulation.status)}`}>
                            {getStatusIcon(simulation.status)}
                            <span className="ml-1 capitalize">{simulation.status}</span>
                          </span>
                          {simulation.status === 'completed' && (
                            <button
                              onClick={(e) => {
                                e.stopPropagation()
                                // Handle download
                              }}
                              className="text-gray-400 hover:text-gray-600"
                            >
                              <Download className="h-4 w-4" />
                            </button>
                          )}
                        </div>
                      </div>
                      
                      {/* Paramtres rapides */}
                      {simulation.input_parameters && (
                        <div className="mt-3 pt-3 border-t border-gray-100">
                          <div className="flex space-x-4 text-sm">
                            {Object.entries(simulation.input_parameters).map(([key, value]) => (
                              <div key={key} className="text-gray-600">
                                <span className="font-medium">{key}:</span> {value}
                              </div>
                            ))}
                          </div>
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Right Panel - Details and Visualization */}
        <div className="space-y-6">
          {/* Simulation Details */}
          {selectedSimulation ? (
            <>
              <div className="bg-white rounded-lg shadow p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4">
                  Dtails de la simulation
                </h3>
                <div className="space-y-4">
                  <div>
                    <label className="text-sm font-medium text-gray-700">Nom</label>
                    <p className="mt-1 text-gray-900">{selectedSimulation.name}</p>
                  </div>
                  <div>
                    <label className="text-sm font-medium text-gray-700">Statut</label>
                    <div className="mt-1">
                      <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getStatusColor(selectedSimulation.status)}`}>
                        {getStatusIcon(selectedSimulation.status)}
                        <span className="ml-1 capitalize">{selectedSimulation.status}</span>
                      </span>
                    </div>
                  </div>
                  <div>
                    <label className="text-sm font-medium text-gray-700">Paramtres</label>
                    <div className="mt-1 p-3 bg-gray-50 rounded text-sm">
                      <pre className="overflow-auto">
                        {JSON.stringify(selectedSimulation.input_parameters, null, 2)}
                      </pre>
                    </div>
                  </div>
                  {selectedSimulation.execution_time && (
                    <div>
                      <label className="text-sm font-medium text-gray-700">
                        Temps d'excution
                      </label>
                      <p className="mt-1 text-gray-900">
                        {selectedSimulation.execution_time} secondes
                      </p>
                    </div>
                  )}
                </div>
              </div>

              {/* Results Visualization */}
              <SimpleResultsVisualization simulation={selectedSimulation} />
            </>
          ) : (
            <div className="bg-white rounded-lg shadow p-6">
              <div className="text-center py-8">
                <BarChart3 className="h-12 w-12 text-gray-400 mx-auto" />
                <h3 className="mt-4 text-lg font-medium text-gray-900">
                  Slectionnez une simulation
                </h3>
                <p className="mt-2 text-gray-600">
                  Cliquez sur une simulation dans la liste pour voir ses dtails
                </p>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

export default Simulations
